import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import { WelcomePage } from '../pages/welcome/welcome';
import { ProfilePage } from '../pages/profile/profile';
import { AddSqueelPage } from '../pages/add-squeel/add-squeel';
import { SettingsPage } from '../pages/settings/settings';
import { GameSqueelsPage } from '../pages/game-squeels/game-squeels';
import { SqueelpopoverPage } from '../pages/squeelpopover/squeelpopover';
import { SetUsernamePage } from '../pages/set-username/set-username';
import { TutorialPage } from '../pages/tutorial/tutorial';
import { CreateAccountPage } from '../pages/create-account/create-account';
import { LoginPage } from '../pages/login/login';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

//Modules
import { Autosize } from '../components/autosize';
import { MomentModule } from 'angular2-moment';


//Apollo config
import { provideClient } from './client';
import { ApolloModule } from 'angular2-apollo';


//Providers
import { Facebook } from '@ionic-native/facebook';
import { TwitterConnect } from '@ionic-native/twitter-connect';
import { NativeStorage } from '@ionic-native/native-storage';
import { Camera } from '@ionic-native/camera';

@NgModule({
  declarations: [
    MyApp,
    Autosize,
    HomePage,
    TabsPage,
    WelcomePage,
    ProfilePage,
    AddSqueelPage,
    SettingsPage,
    GameSqueelsPage,
    SqueelpopoverPage,
    SetUsernamePage,
    TutorialPage,
    CreateAccountPage,
    LoginPage
  ],
  imports: [
    ApolloModule.withClient(provideClient),
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/add-squeel/add-squeel.module#AddSqueelPageModule', name: 'AddSqueelPage', segment: 'add-squeel', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tutorial/tutorial.module#TutorialPageModule', name: 'TutorialPage', segment: 'tutorial', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/set-username/set-username.module#SetUsernamePageModule', name: 'SetUsernamePage', segment: 'set-username', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/create-account/create-account.module#CreateAccountPageModule', name: 'CreateAccountPage', segment: 'create-account', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/welcome/welcome.module#WelcomePageModule', name: 'WelcomePage', segment: 'welcome', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/settings/settings.module#SettingsPageModule', name: 'SettingsPage', segment: 'settings', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile/profile.module#ProfilePageModule', name: 'ProfilePage', segment: 'profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/game-squeels/game-squeels.module#GameSqueelsPageModule', name: 'GameSqueelsPage', segment: 'game-squeels', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/squeelpopover/squeelpopover.module#SqueelpopoverPageModule', name: 'SqueelpopoverPage', segment: 'squeelpopover', priority: 'low', defaultHistory: [] }
  ]
}),
    MomentModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    TabsPage,
    WelcomePage,
    ProfilePage,
    AddSqueelPage,
    SettingsPage,
    GameSqueelsPage,
    SqueelpopoverPage,
    SetUsernamePage,
    TutorialPage,
    CreateAccountPage,
    LoginPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    Facebook,
    TwitterConnect,
    NativeStorage,
    Camera
  ]
})
export class AppModule {}



